import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchMoreImages } from '../actions/searchAction';
import { fetchPage } from '../actions/searchAction';
import { fetchViewerOpen } from '../actions/imgViewerAction';
import { fetchViewerImg } from '../actions/imgViewerAction'; 
import { fetchWaitNextData } from '../actions/imgViewerAction';
import { addToFavorites } from '../actions/searchAction';

import { withStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import IconButton from '@material-ui/core/IconButton';
import CircularProgress from '@material-ui/core/CircularProgress';

import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Close from '@material-ui/icons/Close';
import OpenInNew from '@material-ui/icons/OpenInNew';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import StarIcon from '@material-ui/icons/Star';
import Chat from '@material-ui/icons/Chat';
import PageView from '@material-ui/icons/Pageview';
// import Chat from '@material-ui/icons/Chat';

const styles = {
  backDrop: {
    width: '100%',
    height: '100%',
    top: 0,
    left: 0,
    position: 'absolute',
    cursor: 'zoom-out',
  },
  loadingBarRoot: {
    zIndex: 10000,
    position: 'fixed',
    backgroundColor: 'rgba(0, 0, 0, 0.50)',
    top: 0,
    left: 0,
    width: '100vw',
    height: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  loadingBarProgress: {
    color: '#eee',
  },
  socialData: {
    color: '#ccc',
    fontSize: '1.2em',
    marginLeft: '-6px',
  },
  dialogRoot: {
    backgroundColor: 'rgba(0, 0, 0, 0.84)',
  },
  dialogImg: {
    maxWidth: '80vw',
    maxHeight: '80vh',
    border: '2px solid #fff',
  },
  dialogPaperWidthSm: {
    maxWidth: '80vw',
  },
  imgCounter: {
    // position: 'fixed',
    // top: '2vh',
    // left: '2vw',
    color: '#ccc',
    fontSize: '1em',
    paddingLeft: 12,
  },
  toolBar: {
    position: 'fixed',
    display: 'flex',
    alignItems: 'center',
    boxSizing: 'border-box',
    width: '100%',
    height: '9vh',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
  },
  toolBarTop: {
    top: 0,
    justifyContent: 'space-between',
  },
  toolBarBottom: {
    bottom: 0,
    justifyContent: 'center',
    paddingRight: '10px',
  },
  viewerAdds: {
    position: 'fixed',
    zIndex: 1501,
    height: '100%',
    width: '100%',
    top: 0,
    left: 0,
  },
  viewerAddsNavigation: {
    // position: 'fixed',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    height: '100%',
    // width: '100%',
    // top: 0,
    // left: 0,
  },
  viewerIconButtonLR: {
    display: 'block',
    
    boxSizing: 'border-box',
    width: '45vw',
    height: '40vh',
    padding: 3,
    cursor: 'pointer',
    '&:hover': {
      backgroundColor: 'rgba(0,0,0,0)',
    },
    '&:hover svg': {
      maxWidth: '10vw',
      maxHeight: '10vh',
      height: '1.8em',
      width: '1.8em',
      transition: 'all 0.2s ease',
    },
    zIndex: 1502,
  },
  viewerIconButtonLabel: {
    width: '10vw',
    // justifyContent: 'center',
  },
  viewerIconButtonLabelRight: {
    '& span': {
      float: 'right',
    },
  },
  viewerIconButton: {
    maxHeight: '10vh',
    padding: 1,
    '&:hover svg': {
      maxWidth: '10vh',
      maxHeight: '10vh',
      height: '1.8em',
      width: '1.8em',
      transition: 'all 0.2s ease',
    },
  },
  viewerIconStyleLR: {
    width: '1.3em',
    height: '1.3em',
    maxHeight: 50,
    color: '#ccc',
  },
  viewerIconStyle: {
    color: '#ccc',
    height: '1.3em',
    width: '1.3em',
  },  
};

class ImgViewer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      touchStartX: null,
      touchStartY: null,
      touchStartTime: null,
    };
    this.handleNextButton = this.handleNextButton.bind(this);
    this.handleTouchStart = this.handleTouchStart.bind(this);
    this.handleTouchEnd = this.handleTouchEnd.bind(this);
  }
  
  componentDidMount() {
    window.addEventListener('keydown', this.handleNextButton);
    window.addEventListener('touchstart', this.handleTouchStart);
    window.addEventListener('touchend', this.handleTouchEnd);
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleNextButton);
    window.removeEventListener('touchstart', this.handleTouchStart);
    window.removeEventListener('touchend', this.handleTouchEnd);
  }

  handleTouchStart = (e) => {
    this.setState({
      touchStartX: e.changedTouches[0].clientX,
      touchStartY: e.changedTouches[0].clientY,
      touchStartTime: new Date().getTime(),
    });
  }

  handleTouchEnd = (e) => {
    let touchEndX = e.changedTouches[0].clientX;
    let touchEndY = e.changedTouches[0].clientY;
    let distance = touchEndX - this.state.touchStartX;
    let elapsedTime = new Date().getTime() - this.state.touchStartTime;
    if ((Math.abs(touchEndY - this.state.touchStartY) <= 100) && (Math.abs(distance) >= 50) && (elapsedTime <= 2000)) {
      switch (true) {
        case (distance > 0):
          this.handleNextButton({}, 'left')
          break;
        case (distance < 0):
          this.handleNextButton({}, 'right')
          break;
        default:
          break;
      }  
    }
  }

  // handleImgOpen = (newIndex) => {
  //   this.props.fetchViewerImg(newIndex);
  // }

  handleImgClose = () => {
    this.props.fetchViewerOpen(false, -1);
  }

  handleNextButton = (e = { keyCode: 0, key: '' }, side) => {
    let nextArrInd;

    if ((side === 'right') || (e.key === 'ArrowRight') || (e.keyCode === 39)) {
      nextArrInd = this.props.currentImgInd + 1;
    } else if ((side === 'left') || (e.key === 'ArrowLeft') || (e.keyCode === 37)) {
      nextArrInd = this.props.currentImgInd - 1;
    } else return null

    if (nextArrInd > -1) {
      switch (true) {
        case (nextArrInd < this.props.images.length):
          this.props.fetchViewerImg(nextArrInd);
          break;
        case (nextArrInd === this.props.images.length):
          this.props.fetchWaitNextData(true);
          if (this.props.totalHits > this.props.images.length) {
            this.props.fetchPage(this.props.page + 1);
            this.props.fetchMoreImages(true);
          } else {
            this.handleImgClose()
          }
          break;
        default:
          break;
      }
    } else {
      this.handleImgClose()
    }
  }  

  handleAddToFavorites = (index) => {
    this.props.addToFavorites(index);
    this.forceUpdate();
  }

  formatData = (data) => {
    let formattedData;
    switch (true) {
      case (data<1000):
        formattedData = data;
        break;
      case (data < 1000000):
        formattedData = (data/1000).toFixed(0) + 'K';
        break;
      default:
        formattedData = (data / 1000000).toFixed(0) + 'M';
        break;
    }
    return formattedData
  }

  render() {
    const { classes } = this.props;

    let currentImg = this.props.images[this.props.currentImgInd],
        loadingBar;

    const _loadingBar = (
      <div className={classes.loadingBarRoot}>
        <CircularProgress 
          className={classes.loadingBarProgress} 
          size={50}
        />
      </div>
    );

    if (this.props.nextDataLoading) {
      window.removeEventListener('keydown', this.handleNextButton);
      loadingBar = _loadingBar;
    } else {
      window.addEventListener('keydown', this.handleNextButton);
    }

    let isFavorite = (imgId) => this.props.favorites.findIndex(favImg => favImg.id === imgId);

    return (
      <div className='gridViewer'>
        <Dialog
          classes={{
            root: classes.dialogRoot,
            paperWidthSm: classes.dialogPaperWidthSm,
          }}
          open={this.props.open}
          onClose={this.handleImgClose}
        >
          <img className={classes.dialogImg} src={currentImg.largeImageURL} alt='' />
        </Dialog>
        <div className={classes.viewerAdds}>
          <div className={classes.backDrop} onClick={this.handleImgClose} />
          <div className={classes.viewerAddsNavigation}>
            <IconButton
              onClick={() => this.handleNextButton({}, 'left')}
              // className={classes.viewerIconButtonLR}
              classes={{
                root: classes.viewerIconButtonLR,
                label: classes.viewerIconButtonLabel,
              }}
              // style={{ justifyContent: 'flex-start' }}
              disableRipple
              title='left'
            >
              <ChevronLeft className={classes.viewerIconStyleLR} />
            </IconButton>
            <IconButton
              onClick={() => this.handleNextButton({}, 'right')}
              className={classes.viewerIconButtonLabelRight}
              classes={{
                root: classes.viewerIconButtonLR,
                label: classes.viewerIconButtonLabel,
              }}
              // style={{ justifyContent : 'flex-end' }}
              disableRipple
              title='right'
            >
              <ChevronRight className={classes.viewerIconStyleLR}  />
            </IconButton>
          </div>
          <div 
            className={`${classes.toolBar} ${classes.toolBarTop}`} 
          >
            <div className={classes.imgCounter}>
              {(this.props.currentImgInd + 1) + ' / ' + this.formatData(this.props.totalHits)}
            </div>
            <IconButton
              className={classes.viewerIconButton}
              onClick={this.handleImgClose}
              disableRipple
              title='close'
            >
              <Close className={classes.viewerIconStyle}/>
            </IconButton>
          </div>
          <div 
            className={`${classes.toolBar} ${classes.toolBarBottom}`} 
          >
            <IconButton
              className={classes.viewerIconButton}
              onClick={() => this.handleAddToFavorites(this.props.currentImgInd)}
              title={!~isFavorite(currentImg.id) ? 'add to Favorites' : 'remove from Favorites'}
            >
              {!~isFavorite(currentImg.id) ? <StarBorderIcon className={classes.viewerIconStyle} /> : <StarIcon className={classes.viewerIconStyle} />}
            </IconButton>
            <div className={classes.socialData}> 
              {this.formatData((!~isFavorite(currentImg.id) ? currentImg.favorites : (currentImg.favorites + 1)))} 
            </div>
            <IconButton
              className={classes.viewerIconButton}
              title='read at Pixabay'
              href={currentImg.pageURL}
              target='_blank'
            >
              <Chat className={classes.viewerIconStyle} />
            </IconButton>
            <div className={classes.socialData}> {this.formatData(currentImg.comments)} </div>
            <IconButton
              className={classes.viewerIconButton}
              title='see at Pixabay'
              href={currentImg.pageURL}
              target='_blank'
            >
              <PageView className={classes.viewerIconStyle} />
            </IconButton>
            <div className={classes.socialData}> {this.formatData(currentImg.views)} </div>
            {/* <IconButton
              className={classes.viewerIconButton}
              title='see at Pixabay'
              href={currentImg.pageURL}
              target='_blank'
            >
              <OpenInNew className={classes.viewerIconStyle} />
            </IconButton> */}
          </div>
        </div>
        {loadingBar}
      </div>
    )
  }
}

ImgViewer.propTypes = {
  fetchMoreImages: PropTypes.func,
  fetchPage: PropTypes.func,
  fetchViewerOpen: PropTypes.func,
  fetchViewerImg: PropTypes.func,
  fetchWaitNextData: PropTypes.func,
  addToFavorites: PropTypes.func,
  images: PropTypes.array,
  totalHits: PropTypes.number,
  page: PropTypes.number,
  open: PropTypes.bool,
  currentImgInd: PropTypes.number,
  nextDataLoading: PropTypes.bool.isRequired,
  favorites: PropTypes.array,
  classes: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
  images: state.search.images,
  totalHits: state.search.total,
  page: state.search.page,
  open: state.imgViewer.open,
  currentImgInd: state.imgViewer.currentImgInd,
  nextDataLoading: state.imgViewer.nextDataLoading,
  favorites: state.search.favorites,
})

const mapDispatchToProps = {
  fetchMoreImages,
  fetchPage,
  fetchViewerOpen,
  fetchViewerImg,
  fetchWaitNextData,
  addToFavorites,
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ImgViewer))


