import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import SearchResults from './SearchResults';

import { fetchImages, fetchMoreImages } from '../actions/searchAction';
import { fetchPage } from '../actions/searchAction';
import { fetchViewerOpen } from '../actions/imgViewerAction';
import { fetchSearchText } from '../actions/searchAction';

import { Link } from 'react-router-dom';

class SearchResultsPath extends Component {
  componentDidMount() {
    console.log('SearchResultsPath componentDidMount', this.props.match.params.id);
    this.props.fetchSearchText(this.props.match.params.id);
  }

  render() {
    let { searchText, match } = this.props;
    console.log('SearchResultsPath startRender');
    // this.props.fetchSearchText(match.params.id);
    // if if (searchText)
    return (
      <div>
        <SearchResults />
      </div>
    )
    
  }
}


