import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { fetchImages, fetchMoreImages, cutDownSearchStore } from '../actions/searchAction';
import { fetchPage } from '../actions/searchAction';
import { fetchViewerOpen } from '../actions/imgViewerAction';
import { fetchSearchText } from '../actions/searchAction';
import { addToFavorites } from '../actions/searchAction';

import ImgViewer from './ImageViewer';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';

import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
// import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import CircularProgress from '@material-ui/core/CircularProgress';

// import OpenInNew from '@material-ui/icons/OpenInNew';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import StarIcon from '@material-ui/icons/Star';

const styles = {
  rootComponent: {
    position: 'relative',
    top: 57,
    // top: 114,
    display: 'block',
    margin: '0px 1px',
    overflow: 'hidden',
  },
  barTitleTag: {
    paddingRight: '0.35em',
    cursor: 'pointer',
    textDecoration: 'none',
    color: '#eee',
    '&:hover': {
      textDecoration: 'underline',
    },
  },
  exceededLimitMessage: {
    // display: 'block',
    margin: '10px 0',
    color: '#d50000',
    fontSize: '2em',
    width: '100%',
    textAlign: 'center',
  },
  gridList: {
    justifyContent: 'space-around',
  },
  gridListTile: {
    border: '2px solid rgba(0, 0, 0, 0)',
    borderRadius: '2px',
    transition: 'all 0.1s ease',
    '&:hover': {
      border: '2px solid rgba(0, 0, 0, 0.75)',
    },
    '&:hover .gridListTileBar': {
    // '&:hover ': {
      backgroundColor: 'rgba(0, 0, 0, 0.85)',
    },
  },
  gridImg: {
    cursor: 'zoom-in',
  },
  gridListTileBarRoot: {
    height: 54,
    transition: 'all 0.1s ease',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    boxSizing: 'border-box',

    bottom: 0,
    // left: 0,
    // right: 0,
    width: '100%',
    // height: 48,
    display: 'flex',
    position: 'absolute',
    // background: 'rgba(0, 0, 0, 0.4)',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  gridListTileBarTitleWrap: {
    marginLeft: 12,
    marginRight: 7,

    color: '#fff',
    overflow: 'hidden',
    flexGrow: '0',
    width: '100%',
  },
  gridListTileBarTitle: {
    fontSize: '1em',
    lineHeight: '1.2em',

    overflow: 'hidden',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
  },
  gridListTileBarSubtitle: {
    fontSize: '0.75em',
    lineHeight: '1.2em',

    overflow: 'hidden',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
  },
  iconButtonWrap: {
    // width: 'auto',
    display: 'flex',  
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  iconButton:{
    width: '3em',
    height: '3em',
    fontSize: '0.8em',
    paddingRight: 4, 
    flexGrow: '0',
  },
  icon: {
    fontSize: '2em',
    color: '#fff',
    transition: 'all 0.1s linear',
    '&:hover': {
      fontSize: '3em',
    },
  },
  loadingBarRoot: {
    width: '100vw',
    textAlign: 'center',
  },
  loadingBarProgress: {
    margin: '10px 0',
    color: 'rgb(38, 50, 56)',
  },
  noMatchesWrapper: {
    display: 'flex',
    posititon: 'fixed',
    top: '0',
    left: '0',
    width: '100%',
    height: '80vh',
    alignItems: 'center',
    justifyContent: 'center',
  },
  noMatches: {
    width: '90vw',
    textAlign: 'center',
    fontSize: '2.5em',
    fontWeight: 'bold',
    color: '#aaa',
  },
  socialData: {
    color: '#fff',
    fontSize: '1.2em',
    flexGrow: '0',
    marginRight: '-3px',
    float: 'right',
  },
  waitApiResponse: {
    display: 'flex',
    height: '80vh',
    justifyContent: 'center',
    alignItems: 'center',
  },
};

class SearchResults extends Component {
  constructor(props) {
    super(props);
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    this.props.cutDownSearchStore();
  }

  getSnapshotBeforeUpdate(prevProps, prevState) {
    if (this.props.width !== prevProps.width) {
      // calculate whole document height
      const body = document.body;
      const html = document.documentElement;
      const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
      // return current user position
      let userPosition = window.pageYOffset / docHeight;
      return userPosition;
    }
    return null;
  }

  componentDidUpdate = (prevProps, prevState, snapshot) => {
    if (this.props.width !== prevProps.width) {
      // forceUpdate with timeout is needed for correct calculation of images fit (inside GridListTile) & user position
      setTimeout(() => {
        // calculate whole document height
        const body = document.body;
        const html = document.documentElement;
        const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
        // scroll to correct user position (position before gridList cols number was changed)
        let userPosition = snapshot * docHeight;
        window.scrollTo(0, userPosition);
        this.forceUpdate();
      }, 100);
      }
  }

  handleScroll() {
    if (this.props.totalHits > this.props.images.length) {
      // setTimeout(() => {
        const body = document.body;
        const html = document.documentElement;
        // get height of the window 
        const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
        // calculate whole document height
        const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
        // calculate bottom of window
        const windowBottom = windowHeight + window.pageYOffset;
        // if window bottom is less then 50px to document bottom then add more images
        if ((windowBottom - docHeight) > -50) {
          this.props.fetchPage(this.props.page + 1);
          this.props.fetchMoreImages();
        }
      // }, 100);
    }
  };

  handleImgOpen = (index) => {
    this.props.fetchViewerOpen(true, index);
  }

  handleAddToFavorites = (index) => {
    this.props.addToFavorites(index);
    // refresh 'favorites' icon (StarBorderIcon vs. StarIcon)
    this.forceUpdate();
  }

  handleOnTagClick = (tag) => {
    this.props.fetchSearchText(tag);
    this.props.fetchPage(1);
    this.props.fetchImages();
  }

  formatData = (data) => {
    let formattedData;
    switch (true) {
      case (data < 1000):
        formattedData = data;
        break;
      case (data < 1000000):
        formattedData = (data / 1000).toFixed(0) + 'K';
        break;
      default:
        formattedData = (data / 1000000).toFixed(0) + 'M';
        break;
    }
    return formattedData
  }
  
  render() {
    console.log('Start render SearchResults');

    let imageListContent,
        imageViewer,
        cellHeight,
        loadingBar,
        bottomContent,
        _gridCols;
        

    const { images, totalHits, searchText, favorites, classes, width, mobileWithTouch, waitApiResponse } = this.props;

    const _loadingBar = (
      < div className={classes.loadingBarRoot} >
        <CircularProgress
          className={classes.loadingBarProgress}
          size={50}
        />
      </div >
    );

    loadingBar = () => {
      switch (true) {
        case ((totalHits > images.length) && (images.length < 500)):
          return _loadingBar
        case (totalHits === images.length):
          return null
        default:
          return <div className={classes.exceededLimitMessage}>API limit is exceeded</div> 
      }
    // { ((totalHits > images.length) && (images.length < 500)) ? _loadingBar : <div className={classes.exceededLimitMessage}>API limit is exceeded</div> }
    }
    
    if (this.props.orientation === 'horizontal') {
      cellHeight = 180;
      switch (true) {
        case (width === 'xl'):
          _gridCols = 5
          break;
        case (width === 'lg'):
          _gridCols = 4
          break;
        case (width === 'md'):
          _gridCols = 3
          break;
        case (width === 'sm'):
          _gridCols = 2
          break;
        default:
          _gridCols = 1
          break;
      }
    } else if (this.props.orientation === 'vertical') {
      cellHeight = 270;
      switch (true) {
        case (width === 'xl'):
          _gridCols = 8
          break;
        case (width === 'lg'):
          _gridCols = 6
          break;
        case (width === 'md'):
          _gridCols = 4
          break;
        case (width === 'sm'):
          _gridCols = 3
          break;
        default:
          _gridCols = 2
          break;
      }
    } else {
      // 'else {..}' need update!
      cellHeight = 180;
      switch (true) {
        case (width === 'xl'):
          _gridCols = 5
          break;
        case (width === 'lg'):
          _gridCols = 4
          break;
        case (width === 'md'):
          _gridCols = 3
          break;
        case (width === 'sm'):
          _gridCols = 2
          break;
        default:
          _gridCols = 1
          break;
      }
    }
    // }

    let isFavorite = (imgId) => favorites.findIndex(favImg => favImg.id === imgId);

    if (waitApiResponse) {
      imageListContent = (
        <div className={classes.waitApiResponse}>
          {_loadingBar}
        </div>
      )
    } else if (images.length) {      
    // if (images.length) {      
      imageListContent = (
        <div>
          <GridList
            className={classes.gridList}
            style={{margin: 0}}
            cols={_gridCols} 
            cellHeight={cellHeight}
          >
            {images.map((img, index) => ( 
              <GridListTile 
                className={classes.gridListTile}
                style={{padding: 0}}
                key={index + '_' + img.id} 
              >
                <img
                  className={classes.gridImg}
                  src={img.webformatURL}
                  alt={img.tags}
                  onClick={() => this.handleImgOpen(index)}
                />
                <div className={`${classes.gridListTileBarRoot} ${mobileWithTouch ? null : 'gridListTileBar'}`}>
                  <div className={classes.gridListTileBarTitleWrap}> 
                    <div className={classes.gridListTileBarTitle}>
                      {
                        (() => {
                          let tagArray;
                          if (img.tags.split(',').length > 1) {
                            tagArray = img.tags.split(',')
                          } else {
                            tagArray = img.tags.split(' ')
                          }
                          return (
                            tagArray.map((tag, tagIndex)=> (
                            <span
                            className={classes.barTitleTag}
                            key={tagIndex}
                            title={`search for '${tag.trim()}'`}
                            onClick={() => this.handleOnTagClick(tag.trim())}
                          >
                            #{tag.trim()}
                          </span>
                        )))})()
                      }
                    </div>
                    <div className={classes.gridListTileBarSubtitle}>
                      {
                        <span>
                          by: <a className={classes.barTitleTag}
                            href={`https://pixabay.com/users/${img.user}-${img.user_id}/`}
                            target='_blank'
                            title={`'${img.user}' profile at Pixabay`}
                          >
                            {img.user}
                          </a>
                        </span>
                      }
                    </div>
                  </div>
                  <div>
                    <span className={classes.socialData}>
                      {this.formatData((!~isFavorite(img.id) ? img.favorites : (img.favorites+1)))}
                    </span>
                  </div>
                    <IconButton
                      className={classes.iconButton}
                      onClick={() => this.handleAddToFavorites(index)}
                      title={!~isFavorite(img.id) ? 'add to Favorites' : 'remove from Favorites'}
                    >
                      {!~isFavorite(img.id) ? <StarBorderIcon className={classes.icon} /> : <StarIcon className={classes.icon} />}
                    </IconButton>
                </div>
              </GridListTile  >
            ))}
          </GridList>
          {/* { ((totalHits > images.length) && (images.length < 500)) ? _loadingBar : <div className={classes.exceededLimitMessage}>API limit is exceeded</div> } */}
          {loadingBar()}
        </div>
      )
    } else if (searchText) {
      imageListContent = (
        <div className={classes.noMatchesWrapper}>
          <div className={classes.noMatches}>
            no matches for "{searchText}"
          </div>
        </div>
      )
    }

    if (this.props.open) {
      imageViewer = <ImgViewer />
    }
    
    return (
      <div className={classes.rootComponent}>
        {imageListContent}
        {imageViewer}
      </div>
    )
  }
}

SearchResults.propTypes = {
  fetchImages: PropTypes.func,
  fetchMoreImages: PropTypes.func,
  fetchPage: PropTypes.func,
  fetchViewerOpen: PropTypes.func,
  fetchSearchText: PropTypes.func,
  cutDownSearchStore: PropTypes.func,
  addToFavorites: PropTypes.func,
  images: PropTypes.array,
  totalHits: PropTypes.number,
  page: PropTypes.number,
  orientation: PropTypes.string,
  open: PropTypes.bool,
  searchText: PropTypes.string,
  favorites: PropTypes.array,
  classes: PropTypes.object.isRequired,
  width: PropTypes.string.isRequired,
  mobileWithTouch: PropTypes.bool,
  waitApiResponse: PropTypes.bool.isRequired,
}

const mapStateToProps = state => ({
  images: state.search.images,
  totalHits: state.search.total,
  page: state.search.page,
  orientation: state.search.orientation,
  open: state.imgViewer.open,
  searchText: state.search.searchText,
  favorites: state.search.favorites,
  mobileWithTouch: state.search.mobileWithTouch,
  waitApiResponse: state.search.waitApiResponse,
})

const mapDispatchToProps = {
  fetchImages,
  fetchMoreImages,
  fetchPage,
  fetchViewerOpen,
  cutDownSearchStore,
  addToFavorites,
  fetchSearchText,
}

export default connect(mapStateToProps, mapDispatchToProps)(compose(withStyles(styles), withWidth(), )(SearchResults))


