import { 
  FETCH_IMAGES,
  FETCH_SEARCH_TEXT,
  FETCH_AMOUNT,
  FETCH_ORDER,
  FETCH_ORIENTATION,
  FETCH_IMAGE_TYPE,
  FETCH_CATEGORY,
  FETCH_COLOR,
  FETCH_PAGE,
  FETCH_MORE_IMAGES,
  // FETCH_WINDOW_WIDTH,
  CUT_DOWN_SEARCH_STORE,
  ADD_TO_FAVORITES,
  WAIT_API_RESPONSE
} from './types';

import { fetchViewerImg, fetchViewerOpen, fetchWaitNextData } from './imgViewerAction';

const waitApiResponse = (loading = true) => (dispatch) => {
  console.log('waitApiResponse loading', loading);
  dispatch({
    type: WAIT_API_RESPONSE,
    payload: loading,
  })
}

export const fetchImages = () => (dispatch, getState) => {
  dispatch(waitApiResponse());
  let {
    searchText,
    amount,
    order,
    orientation,
    imageType,
    category,
    color,
  } = getState().search;
  fetch(`https://pixabay.com/api/?key=8913420-89429fef6031f24fd40903778&q=${searchText}&image_type=${imageType}&per_page=${amount}&order=${order}&orientation=${orientation}&category=${category}&colors=${color.join()}&safesearch=true`)
    // .then(response => response.json())
    // .then(response => {
    //   window.scrollTo(0, 0);      
    //   dispatch({
    //     type: FETCH_IMAGES,
    //     payload: [response.hits, response.total]
    //   })
    // })
    .then(response => {
      if (response.ok) {
        return response.json()
      }
    })
    .then(response => {
      window.scrollTo(0, 0);      
      dispatch({
        type: FETCH_IMAGES,
        payload: [response.hits, response.total, false]
      });
      // dispatch(waitApiResponse(false));
    })
    // .then(response => {
    //   if (wait === true) {
    //     dispatch(fetchViewerImg())
    //   }
    // })
    .catch(function (error) {
      console.log('There has been a problem with fetching more images: ', error.message);
    });
}

export const fetchSearchText = (input) => dispatch => {
  dispatch({
    type: FETCH_SEARCH_TEXT,
    payload: input,    
  })
}

export const fetchAmount = (width) => dispatch => {
  let _amount;
  switch (true) {
    case (width === 'xl'):
      _amount = 80;
      break;
    case ((width === 'lg') || (width === 'md') || (width === 'sm')):
      _amount = 60;
      break;
    default:
      _amount = 30;
      break;
  }
  dispatch({
    type: FETCH_AMOUNT,
    payload: _amount,
  })
}

export const fetchOrder = (input) => dispatch => {
  dispatch({
    type: FETCH_ORDER,
    payload: input,
  })
}

export const fetchOrientation = (input) => dispatch => {
  dispatch({
    type: FETCH_ORIENTATION,
    payload: input,
  })
}

export const fetchImageType = (input) => dispatch => {
  dispatch({
    type: FETCH_IMAGE_TYPE,
    payload: input,
  })
}

export const fetchCategory = (input) => dispatch => {
  dispatch({
    type: FETCH_CATEGORY,
    payload: input,
  })
}

export const fetchColor = (input = [null]) => dispatch => {
  dispatch({
    type: FETCH_COLOR,
    payload: input,
  })
}

export const fetchPage = (input) => dispatch => {
  dispatch({
    type: FETCH_PAGE,
    payload: input,
  })
}

export const fetchMoreImages = (wait = false) => (dispatch, getState) => {
  let {
    searchText,
    amount,
    order,
    orientation,
    imageType,
    category,
    color,
    page,
  } = getState().search;
  fetch(`https://pixabay.com/api/?key=8913420-89429fef6031f24fd40903778&q=${searchText}&image_type=${imageType}&per_page=${amount}&order=${order}&orientation=${orientation}&category=${category}&colors=${color.join()}&safesearch=true&page=${page}`)
    .then(response => {
      if (response.ok) {
        return response.json()
      }
    })
    .then(response => dispatch({
      type: FETCH_MORE_IMAGES,
      payload: response.hits
    })
    )
    .then(response => {
      if(wait === true){
        dispatch(fetchViewerImg())
      }
    })
    .catch(function (error) {
      dispatch(fetchViewerOpen(false, -1));
      dispatch(fetchWaitNextData(false));
      console.log('There has been a problem with fetching more images: ', error.message);
    });
}

