import { FETCH_VIEWERIMG, FETCH_VIEWEROPEN, FETCH_WAIT_NEXT_DATA, UPDATE_RECENTLY_WATCHED } from './types';

export const fetchViewerOpen = (open, currentImgInd) => (dispatch) => {
  dispatch({
    type: FETCH_VIEWEROPEN,
    payload: [open, currentImgInd],
  });
  if (open) {
    dispatch(updateRecentlyWatched(currentImgInd))
  }
}

export const fetchViewerImg = (newImgInd = null) => (dispatch, getState) => {
  if(newImgInd === null) {
    newImgInd = getState().imgViewer.currentImgInd + 1;
  }
  dispatch({
    type: FETCH_VIEWERIMG,
    payload: newImgInd,
  });
  dispatch(updateRecentlyWatched(newImgInd))
}

export const fetchWaitNextData = (loading = true) => (dispatch) => {
  dispatch({
    type: FETCH_WAIT_NEXT_DATA,
    payload: loading,
  })
}

export const updateRecentlyWatched = (ImgInd) => (dispatch, getState) => {
  let currentImg = getState().search.images[ImgInd];
  let recentlyWatchedImgs = getState().imgViewer.recentlyWatchedImgs;
  let filteredRecentlyWatchedImgs = recentlyWatchedImgs.filter(img => (img.id !== currentImg.id));
  dispatch({
    type: UPDATE_RECENTLY_WATCHED,
    payload: [currentImg,...filteredRecentlyWatchedImgs],
  })
}  


