import { 
  FETCH_IMAGES,
  FETCH_SEARCH_TEXT,
  FETCH_AMOUNT,
  FETCH_ORDER,
  FETCH_ORIENTATION,
  FETCH_IMAGE_TYPE,
  FETCH_CATEGORY,
  FETCH_COLOR,
  FETCH_PAGE,
  FETCH_MORE_IMAGES,
  // FETCH_WINDOW_WIDTH,
  CUT_DOWN_SEARCH_STORE,
  ADD_TO_FAVORITES,
  WAIT_API_RESPONSE,
} from '../actions/types';

// // determine 'designType' before all components rendering 
// let _designType, _amount;
// switch (true) {
//   case (window.innerWidth >= 1600):
//     _designType = 'xl';
//     _amount = 80;
//     break;
//   case (window.innerWidth >= 1024):
//     _designType = 'lg';
//     _amount = 60;
//     break;
//   case (window.innerWidth >= 768):
//     _designType = 'md';
//     _amount = 60;
//     break;
//   case (window.innerWidth >= 480):
//     _designType = 'sm';
//     _amount = 60;
//     break;
//   default:
//     _designType = 'xs';
//     _amount = 30;
//     break;
// }

// define mobile devices with touch screen
let _mobileWithTouch = (/Mobi/i.test(navigator.userAgent) || /Android/i.test(navigator.userAgent));

const initialState = {
  searchText: '',
  order: 'popular',
  orientation: 'horizontal',
  imageType: 'photo',
  category: '',
  color: [null],
  images: [],
  page: 1,
  total: 0,

  amount: 30,
  // designType: _designType,
  mobileWithTouch: _mobileWithTouch,
  favorites: [],
  waitApiResponse: false,
};

export default function(state = initialState, action) {
  switch (action.type) {
    case FETCH_SEARCH_TEXT:
      return Object.assign({}, state, {
        searchText: action.payload,
      })
    case FETCH_IMAGES:
      return Object.assign({}, state, {
        images: action.payload[0],
        total: action.payload[1],
        page: 1,
        waitApiResponse: false,
      })
    case FETCH_AMOUNT:
      return Object.assign({}, state, {
        amount: action.payload,
      })
    case FETCH_ORDER:
      return Object.assign({}, state, {
        order: action.payload,
      }) 
    case FETCH_ORIENTATION:
      return Object.assign({}, state, {
        orientation: action.payload,
      }) 
    case FETCH_IMAGE_TYPE:
      return Object.assign({}, state, {
        imageType: action.payload,
      })
    case FETCH_CATEGORY:
      return Object.assign({}, state, {
        category: action.payload,
      })
    case FETCH_COLOR:
      return Object.assign({}, state, {
        color: action.payload,
      })
    case FETCH_PAGE:
      return Object.assign({}, state, {
        page: action.payload,
      })
    case FETCH_MORE_IMAGES:
      return Object.assign({}, state, {
        images: state.images.concat(action.payload),
      })
    // case FETCH_WINDOW_WIDTH:
    //   return Object.assign({}, state, {
    //     designType: action.payload,
    //   })
    case CUT_DOWN_SEARCH_STORE:
      return Object.assign({}, state, {
        images: state.images.slice(0, state.amount),
        page: 1,
      })
    case ADD_TO_FAVORITES:
      return Object.assign({}, state, {
        favorites: action.payload,
      })
    case WAIT_API_RESPONSE:
      return Object.assign({}, state, {
        waitApiResponse: action.payload,
      })
    default:
      return state;
  }
}


